import { useEffect } from "react";
import { useLocation } from "react-router-dom";
import ReactGA from "react-ga4";

const usePageTracking = () => {
  const location = useLocation();
  const params = new Proxy(new URLSearchParams(location.search), {
    get: (searchParams, prop) => searchParams.get(prop)
  });
  // Get the value of "some_key" in eg "https://example.com/?some_key=some_value"
  let via = params.via; // "some_value"
  useEffect(() => {
    // Google Analytics Ê∏¨ÂÆö ID „ÇíÂÖ•Âäõ„Åó„Å¶Ë®≠ÂÆö
    ReactGA.initialize("G-Y01YEEJWFQ");
    ReactGA.send({
      hitType: "pageview",
      // „Ç¢„ÇØ„Çª„Çπ„Åó„Åü„Éë„Çπ (pathname) „Å®„ÇØ„Ç®„É™ÊñáÂ≠óÂàó (search) „ÇíÈÄÅ‰ªò„Åô„Çã (ÂøÖË¶Å„Å´Âøú„Åò„Å¶Á∑®ÈõÜ„Åô„Çã)
      page: location.pathname + location.search
    });
    if (via === "qr") {
      ReactGA.event("via_qr", {
        value: "QRÁµåÁî±"
      });
    }
  }, [location, via]);
};

export default usePageTracking;
